import { formatToRuTimeDayMonthYear } from "@/lib/dateUtils"
import Image from "next/image";

export default async function NewsDetail({ params }: { params: Promise<{ id: string }> }) {
    const { id } = await params

    const data = {
        src: '',
        title: `Название новости ${(await params).id} (Ижевск, с 265-летием!)`,
        date: '2025-01-01T16:40:00',
        newsDescription: "Подпись к новости. Подпись к новости. Подпись к новости. Подпись к новости. Подпись к новости. Подпись к новости. Подпись к новости. Подпись к новости.",
        alt: ''
    }

    const correctDateFormat = formatToRuTimeDayMonthYear(data.date, true);

    return (
        <div className="w-full flex flex-col items-center mt-[50px]">
            <div className="w-full max-w-[1200px] flex flex-col justify-center">
                <div className="w-full">
                    <p className="w-full text-[20px] font-medium text-white leading-none">
                        {correctDateFormat}
                    </p>
                </div>
                <div className="w-full mt-[60px]">
                    <h1 className="w-full text-[60px] font-extrabold text-white leading-none">
                        {data.title}
                    </h1>
                </div>
                <div className="w-full mt-[0px]">
                    <h1 className="w-full text-[30px] font-medium text-white leading-none">
                        {data.newsDescription}
                    </h1>
                </div>
                <div className="w-full mt-[0px]">
                    <Image alt={data.alt} src={data.src ? data.src : ''}/>
                </div>
            </div>
        </div>
    )
}